import * as React from 'react';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import FormControl from '@mui/material/FormControl';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import Select from '@mui/material/Select';
import TextField from '@mui/material/TextField';
import Typography from '@mui/material/Typography';
import { Property } from 'csstype';

interface IContactValues {
  name: string;
  tel: string;
  email: string;
  inquiry: string;
}

export const Contact = () => {
  const [isSubmitted, setIsSubmitted] = React.useState<boolean>(false);
  const [isValidContact, setIsValidContact] = React.useState<boolean>(false);
  const [contactValues, setContactValues] = React.useState<IContactValues>({
    name: '',
    tel: '',
    email: '',
    inquiry: ''
  });
  // const [isAgreePrivacy, setIsAgreePrivacy] = React.useState<0 | 1>(0);
  const [isAgreePrivacy, setIsAgreePrivacy] = React.useState<0 | 1>(1);

  React.useEffect(() => {
    setIsValidContact(
      !!(
        contactValues.name &&
        contactValues.email &&
        contactValues.inquiry &&
        isAgreePrivacy === 1
      )
    );
  }, [contactValues, isAgreePrivacy]);

  return (
    <Box
      mt={8}
      sx={{
        '.MuiInputBase-input': {
          color: 'text.primary'
        }
      }}
    >
      <Typography variant='h4' mb={2}>
        
      </Typography>

      <Box
        maxWidth='lg'
        bgcolor='background.paper'
        mb={10}
        p={{ sm: 8, xs: 2 }}
      >
        <Box maxWidth='md' mx='auto'>
          <Box>
            {isSubmitted ? (
              <Typography>
                
                <br />
                
              </Typography>
            ) : (
              <form
                action='https://docs.google.com/forms/u/1/d/e/1FAIpQLSfGdxMA6tQuZ9wa7uihlROhN2Na_Mnp5rfMez19aPEwZ_op7g/formResponse'
                method='post'
                target='onSubmitIframe'
                onSubmit={e => {
                  console.log('==== onsubmit ===');
                  console.log(e);

                  if (!isValidContact) {
                    e.preventDefault();
                    return false;
                  }
                }}
              >
                {[
                  {
                    id: 'name',
                    type: 'text',
                    name: 'entry.1612581729',
                    label: ''
                  },
                  {
                    id: 'tel',
                    type: 'tel',
                    name: 'entry.1208032875',
                    label: '',
                    required: false
                  },
                  {
                    id: 'email',
                    type: 'email',
                    name: 'entry.444215818',
                    label: ''
                  }
                ].map(
                  ({
                    required = true,
                    id,
                    type,
                    name,
                    label
                  }: {
                    required?: boolean;
                    id: string;
                    type: string;
                    name: string;
                    label: string;
                  }) => (
                    <TextField
                      required={required}
                      key={id}
                      id={id}
                      type={type}
                      name={name}
                      label={label}
                      variant='outlined'
                      value={(contactValues as any)[id]}
                      fullWidth
                      sx={{
                        my: 2
                      }}
                      onChange={e =>
                        setContactValues({
                          ...contactValues,
                          [id]: e.target.value
                        })
                      }
                    />
                  )
                )}
                <TextField
                  required
                  id='inquiry'
                  name='entry.1449459852'
                  label=''
                  variant='outlined'
                  multiline
                  rows={6}
                  fullWidth
                  sx={{
                    my: 2
                  }}
                  onChange={e =>
                    setContactValues({
                      ...contactValues,
                      inquiry: e.target.value
                    })
                  }
                />
                {/*
                <FormControl fullWidth>
                  <InputLabel id='demo-simple-select-label'>
                    
                  </InputLabel>
                  <Select
                    labelId='demo-simple-select-label'
                    id='demo-simple-select'
                    value={isAgreePrivacy}
                    label=''
                    onChange={e => {
                      setIsAgreePrivacy(Number(e.target.value) === 1 ? 1 : 0);
                    }}
                  >
                    <MenuItem value={0}></MenuItem>
                    <MenuItem value={1}></MenuItem>
                  </Select>
                </FormControl>
                */}

                <Button
                  disabled={!isValidContact}
                  type='submit'
                  variant='contained'
                  size='large'
                  disableElevation={true}
                  sx={{
                    display: 'block',
                    mx: 'auto',
                    my: 2,
                    px: 4,
                    borderRadius: '0px'
                  }}
                >
                  
                </Button>
              </form>
            )}
            <iframe
              title='iframe'
              name='onSubmitIframe'
              style={{ display: 'none' }}
              onLoad={() => {
                setIsSubmitted(true);
              }}
            />
          </Box>
        </Box>
      </Box>
    </Box>
  );
};
